<template>
  <div class="business-detail-page">
    <div class="content-header">
      <bread-crumb
        current-level="业务对象详情"
        upper-level="业务对象"
        back-path="/main/business?back"
      />
    </div>
    <div class="business-detail-body">
      <!-- <gs-loading :is-show="isDetailLoading" /> -->
      <div class="business-header">{{ detailData.name || '-' }}</div>
      <two-column-layout>
        <div
          slot="left"
          class="business-info"
        >
          <gs-loading :is-show="businessInfoLoading" />
          <div class="info-header">
            <div class="info-header-title">
              <img
                :src="businessImgUrl"
                class="info-header-title-img"
              >
              <div class="info-header-title-info">
                <div class="info-header-title-text">业务信息</div>
                <div class="info-item has-two-value">
                  <span class="info-item-label">业务部门</span>
                  <span class="info-item-value">{{ departmentName || '-' }}</span>
                  <span class="info-item-label second-label">业务模块</span>
                  <span class="info-item-value">{{ detailData.module || '-' }}</span>
                </div>
                <div class="info-item">
                  <span class="info-item-label">业务描述</span>
                  <span class="info-item-value">{{ detailData.description || '-' }}</span>
                </div>
              </div>
            </div>
            <div class="info-headr-action">
              <!-- <gs-button type="text-primary">编辑</gs-button> -->
              <edit-business
                :disabled="!detailData.editBusiness"
                :business-id="businessId"
                :business-obj="detailData"
                @edit-success="getBusinessDetail({params: businessId, isManage: false})"
              />
            </div>
          </div>
          <div class="info-body">
            <div class="info-item">
              <span class="info-item-label">所有者</span>
              <span class="info-item-value">{{ detailData.owner || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">管理者</span>
              <span class="info-item-value">{{ detailData.manager || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">维护者</span>
              <span class="info-item-value">{{ detailData.maintainer || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">相关数据资产</span>
              <span class="info-item-value">{{ detailData.dataAssets || '-' }}</span>
            </div>
            <div class="info-item has-two-value">
              <span class="info-item-label">最后更新时间</span>
              <span class="info-item-value">{{ detailData.businessLastUpdate || '-' }}</span>
              <span class="info-item-label second-label">更新人</span>
              <span class="info-item-value">{{ detailData.businessOperator || '-' }}</span>
            </div>
          </div>
        </div>
        <div
          slot="right"
          class="api-info"
        >
          <div class="info-header">
            <div class="info-header-title">
              <img
                :src="apiImgUrl"
                class="info-header-title-img"
              >
              <div class="info-header-title-info">
                <div class="info-header-title-text">API信息(Coming Soon)</div>
              </div>
            </div>
          </div>
          <div class="info-body">
            <i class="iconfont icon-kong none-icon" />
            <div class="info-text">无任何API信息</div>
          </div>
        </div>
      </two-column-layout>
      <div class="tech-info">
        <gs-loading :is-show="techInfoLoading" />
        <div class="tech-info-header">
          <div class="tech-info-header-text">
            <span class="title-text">
              <i class="iconfont icon-jishus tech-icon" />
              <span class="header-text">技术信息</span>
            </span>
            <span class="update-text">
              最后更新时间：
              <span>{{ techInfo.technicalLastUpdate || '-' }}</span>
              <span class="update-text update-person">更新人：</span>
              {{ techInfo.technicalOperator || '-' }}
            </span>
          </div>
          <div class="tech-info-header-action">
            <!-- <gs-button type="text-primary" :disabled="!techInfo.editTechnical">编辑</gs-button> -->
            <edit-technology
              :disabled="!techInfo.editTechnical"
              :category-guid="businessId"
              :tech-tables="tableData"
              @edit-success="handleEditTech"
            />
          </div>
        </div>
        <div class="tech-info-table">
          <el-table
            :data="tableData"
            :row-class-name="activeTableClassName"
            style="width: 100%"
            max-height="250"
            @row-click="handleSelectTable"
          >
            <el-table-column
              prop="tableName"
              label="表名称"
            />
            <el-table-column
              prop="dbName"
              label="所属库"
            />
            <el-table-column
              prop="createTime"
              label="表创建时间"
            />
          </el-table>
        </div>
      </div>
      <div
        v-loading="tableInfoLoading"
        class="table-info"
      >
        <div class="prev-action action-block">
          <gs-button
            :disabled="disabledPrev"
            type="text-primary"
            @click="handlePrevTable"
          >
            <gs-icon
              name="left"
              class="action-icon"
              title="上一个"
            />
          </gs-button>
        </div>
        <div class="table-info-base">
          <div class="table-info-base-header">
            <i class="iconfont icon-shujubiao- table-icon" />
            <span class="header-text">数据表信息</span>
          </div>
          <div
            v-if="tableInfo.databaseName"
            class="table-info-base-body"
          >
            <div class="info-item">
              <span class="info-item-label">数据库</span>
              <span class="info-item-value">{{ tableInfo.databaseName || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">表名称</span>
              <span
                class="info-item-value"
              >{{ tableInfo.tableName || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">表类型</span>
              <span class="info-item-value">{{ tableInfo.type || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">表标签</span>
              <span class="info-item-value">
                <gs-tag
                  v-for="item in tableInfo.tags"
                  :key="item.tagId"
                  :title="item.tagName"
                >{{ item.tagName }}</gs-tag>
                <span v-if="!tableInfo.tags || !tableInfo.tags.length">-</span>
              </span>
            </div>
            <div class="info-item">
              <span class="info-item-label">创建人</span>
              <span class="info-item-value">{{ tableInfo.owner || '-' }}</span>
            </div>
            <div class="info-item">
              <span class="info-item-label">创建时间</span>
              <span class="info-item-value">{{ tableInfo.createTime || '-' }}</span>
            </div>
          </div>
          <div
            v-else
            class="no-data"
          >
            <i class="iconfont icon-kong none-icon" />
            <div class="info-text">无任何数据表信息</div>
          </div>
        </div>
        <div class="table-info-column">
          <div class="table-info-column-header">
            <i class="iconfont icon-ziduanshezhi column-icon" />
            <span class="header-text">字段信息</span>
          </div>
          <div
            v-if="tableInfo.columns && tableInfo.columns.length"
            class="table-info-column-body"
          >
            <div class="column-text">字段名称</div>
            <div class="column-name">
              <span
                v-for="item in tableInfo.columns"
                :key="item.columnId"
                class="column-name-item"
              >{{ item.columnName }}</span>
              <!-- <div class="no-data" v-if="!tableInfo.columns || !tableInfo.columns.length">暂无选中的表</div> -->
            </div>
          </div>
          <div
            v-else
            class="no-data"
          >
            <i class="iconfont icon-kong none-icon" />
            <div class="info-text">无任何字段信息</div>
          </div>
        </div>
        <div class="next-action action-block">
          <!-- <gs-icon name="right" class="action-icon" title="下一个" /> -->
          <gs-button
            :disabled="disabledNext"
            type="text-primary"
            @click="handleNextTable"
          >
            <gs-icon
              name="right"
              class="action-icon"
              title="下一个"
            />
          </gs-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import * as tableInfoService from 'src/service/business-obj/technology';
import DataTable from 'src/components/data-table';
import TwoColumnLayout from 'src/components/two-column-layout';
import EditBusiness from './edit-business';
import EditTechnology from './edit-technology';
import BreadCrumb from 'src/components/breadcrumb';
import {
  checkLoading
} from 'src/store/status';
import './style';

export default {
  components: {
    DataTable,
    TwoColumnLayout,
    EditBusiness,
    EditTechnology,
    BreadCrumb
  },

  data() {
    return {
      businessImgUrl: require('../../../../public/images/business-info.png'),
      apiImgUrl: require('../../../../public/images/business-api.png'),
      businessId: '',
      departmentName: '',
      activeTable: {},
      activeTableIndex: -1,
      tableInfo: {},
      tableInfoLoading: false
    };
  },

  computed: {
    ...mapState('business', {
      businessDetail: 'businessDetail',
      techDetail: 'techDetail'
    }),
    detailData() {
      return this.businessDetail.data;
    },
    techInfo() {
      return this.techDetail.data;
    },
    tableData() {
      return this.techInfo.tables || [];
    },
    isDetailLoading() {
      return checkLoading(this.businessDetail.status);
    },
    businessInfoLoading() {
      return checkLoading(this.businessDetail.status);
    },
    techInfoLoading() {
      return checkLoading(this.techDetail.status);
    },
    disabledPrev() {
      return this.activeTableIndex === 0 || !this.tableData.length;
    },
    disabledNext() {
      return this.activeTableIndex === this.tableData.length - 1 || !this.tableData.length;
    }
  },

  watch: {
    'activeTable.tableGuid'(val) {
      if (val) this.getTableInfo(val);
    }
  },

  /**
   * 路由离开触发
   */
  beforeRouteLeave(to, from, next) {
    // 判断点击返回元数据列表时，进入缓存界面
    if ((to.path === '/main/business') && to.query.back === null) {
      to.meta.keepAlive = true;
    }
    next();
  },

  mounted() {
    const params = this.$route.params || {};
    const query = this.$route.query || {};
    const id = params.id;
    const dp = query.dp || '';
    this.businessId = id;
    this.departmentName = dp;
    if (id) {
      this.getBusinessDetail({params: id, isManage: false});
      this.getTechDetail({params: id, isManage: false}).then(res => {
        if (res.tables && res.tables.length) {
          this.activeTable = res.tables[0];
          this.activeTableIndex = 0;
        }
      });
    }
  },

  methods: {
    ...mapActions('business', [
      'getBusinessDetail',
      'getTechDetail'
    ]),
    activeTableClassName({row, rowIndex}) {
      const {
        activeTable
      } = this;
      if (!activeTable.tableGuid && rowIndex === 0) {
        return 'active-row';
      } else if (row.tableGuid === activeTable.tableGuid) {
        return 'active-row';
      }
      return '';
    },
    handleEditTech() {
      this.getTechDetail({params: this.businessId, isManage: false}).then(res => {
        const tables = res.tables || [];
        if (tables.length) {
          this.activeTable = tables[0];
          this.activeTableIndex = 0;
        } else {
          this.activeTable = {};
          this.activeTableIndex = -1;
        }
      });
    },
    handleSelectTable(row) {
      this.activeTable = row;
      this.tableData && this.tableData.map((item, i) => {
        if (item.tableGuid === row.tableGuid) this.activeTableIndex = i;
      });
    },
    handlePrevTable() {
      this.activeTableIndex--;
      const {
        activeTableIndex
      } = this;
      this.activeTable = this.tableData[activeTableIndex];
    },
    handleNextTable() {
      this.activeTableIndex++;
      const {
        activeTableIndex
      } = this;
      this.activeTable = this.tableData[activeTableIndex];
    },
    /**
     * 查询表详情列表
     */
    getTableInfo(id) {
      this.tableInfoLoading = true;
      tableInfoService.getTableInfo(false, id).then((res) => {
        this.tableInfoLoading = false;
        this.tableInfo = res;
      }).catch((res) => {
        this.tableInfoLoading = false;
      });
    },
    jump2table() {
      const id = this.activeTable.tableGuid;
      this.$router.push({
        path: `/main/metadata/detail/${id}`
      });
    }
  }
};
</script>




